.saved h2{
    margin-left: 30px;
    font-size: 26px;
    color: rgb(52, 51, 51);
    padding-top: 15px;
}
.saved ul{
    margin-left: 30px;
    font-size: 23px;
    color: rgb(52, 51, 51);
    font-weight: 500;
}
.saved{
    height: 1000px;
    width: 105%;
    background-color: rgb(200, 189, 189);
}
@media screen and (min-width:330px) and (max-width:650px) {
    .saved h2 {
      font-size: 20px;
      margin-left: 15px;
    }
    .saved ul {
      font-size: 18px;
      margin-left: 15px;
    }
    .saved{
        width: 234%;
    }
  }